let operation_config = new Map();
/**
 * access_records
*/
const access_records = [
    //
    { name: "", property: "name", type: 'input', width: '80', },
    { name: "", property: "code", type: 'input', width: '100', },
    { name: "", property: "date", type: "date", width: '30', },
    {
        name: "",
        property: "passageResult",
        type: 'select',
        width: '110',
        options: [
            { value: '1', label: '' },
            { value: '0', label: '' },
        ]
    },
    {
        name: "",
        property: "divergenceType",
        type: 'select',
        width: '110',
        options: [
            { value: '0', label: '' },
            { value: '1', label: '' },
        ]
    },
    {
        name: "",
        property: "personType",
        type: 'select',
        width: '115',
        options: [
            { value: '0', label: '' },
            { value: '1', label: '' },
            { value: '2', label: '' },
        ]
    },
]

const access_records_button = {
    delete: true, search: true, add: false
}

operation_config.set('access_records', access_records);
operation_config.set('access_records_button', access_records_button);

/**
 * operation_log
*/
const operation_log = [
    //
    { name: "", property: "name", type: 'input', width: '80' },
    { name: "", property: "id", type: 'input', width: '100' },
    { name: "", property: "hour", type: "date", width: '30' },
    { name: "", property: "verification", type: 'input', width: '110' },
]

const operation_log_button = {
    delete: false, search: true, add: false
}

operation_config.set('operation_log', operation_log);
operation_config.set('operation_log_button', operation_log_button);

/**
 * operation_log
*/
const people_manage = [
    //
    { name: "", property: "name", type: 'input', width: '80' },
    { name: "", property: "id", type: 'input', width: '100' },
    { name: "", property: "hour", type: "options", width: '30' },
]

operation_config.set('people_manage', people_manage);

/**
 *alarm_log()
*/
const alarm_log = [
    {
        name: "",
        property: "eventLevel",
        type: 'select',
        width: '110',
        options: [
            { value: '1', label: '1' },
            { value: '2', label: '2' },
            { value: '3', label: '3' },
            { value: '4', label: '4' }
        ]
    },
    { name: "", property: "eventName", type: 'input', width: '120',},
    { name: "", property: "eventTime", type: 'date', width: '100' }
]
operation_config.set('alarm_log', alarm_log);

export default operation_config



