<template>
    <div class="separator">
        <el-breadcrumb separator-class="el-icon-arrow-right" class="bread">
            <!-- <el-breadcrumb-item
                v-for="(item,index) in this.$route.matched"
                :key="index"
                :to="{ path: index= 1 ? item.path: '/' }"
                >{{item.name}}</el-breadcrumb-item> -->
                <el-breadcrumb-item
                v-for="(item,index) in breadList"
                :key="index"
                :to="{ path: index= 0 ? '/' : null }"
                >{{$i18n.locale=='en'?item.meta.enName:item.name}}</el-breadcrumb-item>
        </el-breadcrumb>
    </div>
</template>
<script>
export default {
  data () {
    return {
      name: 'a',
      breadList: []
    }
  },
  created() {
    Object.assign(this.breadList, this.$route.matched)
  },
  mounted () {
    // console.log(this.$route.matched)
  }
}
</script>
<style scoped>
.separator {
  width: 100%;
  height: 24px;
}
.bread{
  height: 100%;
}
</style>



