<template>
  <el-tree
    :data="groupType"
    :props="defaultProps"
    :highlight-current="true"
    @node-click="handleNodeClick"
    lazy
    :load="loadNode"
    class="menu-tree"
  >
  </el-tree>
</template>

<script>
import api from "../../../../../config/api.js";
export default {
  name: "theTree",
  props: ["isSelect", "isNeedlinkage", "platform", "isLeftBar"],
  data() {
    return {
      selectdialogVisible: false,
      search: "",
      groupType: [],
      netAPI: "",
      monitorAPI: '',
      searchgroup: "1005",
      postBooleanToParent: false,
      curr: "",
      defaultProps: {
        children: "children",
        label: "label"
      },
      groupDeviceNum: ''
    };
  },
  created() {
    this.netAPI = api().get("device_group");
    this.netAPI2 = api().get("accessSystem");
    this.netAPI3 = api().get("device");
    this.monitorAPI = api().get("monitorSystem").deviceManage;
  },
  mounted() {
    this.$EventBus.$on('initTree', () => {
      this.initData();
    })
  },
  methods: {
    initData() {
      this.groupType = [
        {
          label: this.$i18n.locale == 'zh' ? '' : 'Device group',
          groupNo: "1005",
          groupName: this.$i18n.locale == 'zh' ? '' : 'Device group',
          parentNo: this.$i18n.locale == 'zh' ? '' : 'null',
          description: this.$i18n.locale == 'zh' ? '' : 'Root group'
        }
      ];
    },
    // 
    loadNode(node, resolve) {
      if (node.level == 0) {
        return resolve([
          {
            // label: "",
            // groupNo: "1005",
            // groupName: "",
            // parentNo: "",
            // description: ""
            label: this.$i18n.locale == 'zh' ? '' : 'Device group',
            groupNo: "1005",
            groupName: this.$i18n.locale == 'zh' ? '' : 'Device group',
            parentNo: this.$i18n.locale == 'zh' ? '' : 'null',
            description: this.$i18n.locale == 'zh' ? '' : 'Root group'
          }
        ]);
      }
      let groupNo = "";
      if (node.data.groupNo) {
        groupNo = node.data.groupNo;
      }
      this.$http
        .get(this.netAPI.find, {
          params: {
            groupNo
          }
        })
        .then(res => {
          if (res.body.status == 200) {
            let newgroupType = [];
            res.body.data.forEach(item => {
              // 
              if(this.platform == 'coala') {
                // 
                if(this.isSelect == 'true') {
                  newgroupType.push({
                    label: item.groupName,
                    groupNo: item.groupNo,
                    id: item.id,
                    description: item.description,
                    parentNo: item.parentNo
                  });
                }else{
                  newgroupType.push({
                    label: item.groupName + ` <${item.deviceNumber}>`,
                    groupNo: item.groupNo,
                    id: item.id,
                    description: item.description,
                    parentNo: item.parentNo
                  });
                }
              }else{
              // 
                if(item.deviceNumber > 0) {
                  // newgroupType.push({
                  //   label: item.groupName + ` <${item.deviceNumber}>`,
                  //   groupNo: item.groupNo,
                  //   id: item.id,
                  //   description: item.description,
                  //   parentNo: item.parentNo
                  // });
                  this.$http.get(this.monitorAPI.find, {
                    params: {
                      groupNo: item.groupNo
                    }
                  }).then(res => {
                    if(res.body.status == 200) {
                      if(res.body.total > 0) {
                        newgroupType.push({
                          label: item.groupName + ` <${res.body.total}>`,
                          groupNo: item.groupNo,
                          id: item.id,
                          description: item.description,
                          parentNo: item.parentNo
                        });
                        resolve(newgroupType);
                      }
                    }
                  })
                }
              }
            });
            resolve(newgroupType);
          }
        });
    },
    // 
    handleNodeClick(data, node) {
     
      
      // console.log(JSON.stringify(data));
      // console.log(node);
      this.$store.default.state.accessManage.groupData = data
      const promise = new Promise((resolve, reject) => {
        this.$http
          .get(this.netAPI2.accessDevice.findAllDevice, {
            params: {
              groupNo: data.groupNo,
              type: this.$store.default.state.accessManage.deviceType
            }
          })
          .then(res => {
            if (res.body.status == 200) {
              this.$store.default.state.accessManage.deviceList = res.body.data;
              resolve(res.body.data)
            } else {
              this.$message.error(res.body.message);
            }
          });
      });
      promise.then(res => {
        this.getAddDeviceList()
      })
      this.$store.default.commit('freshData')
      this.curr = data;
      // 
      if(this.platform == 'coala') {
        this.$emit("receiveChildProps", this.postBooleanToParent, this.curr);
        this.$emit("batchgroupProps", this.curr);
      }else{
        this.$emit("monitorLinkage", data)
      }
      // 
      if (this.isNeedlinkage == "true" && this.platform == 'coala') {
        this.$EventBus.$emit("linkage", data);
      }
    },
    //
    getAddDeviceList() {
      this.$http
        .get(this.netAPI2.accessDevice.findDeviceAll, {
          params: {
            pageSize: 1000,
            pageNo: 1
          }
        })
        .then(res => {
          for (const key in res.body.data) {
            if (res.body.data.hasOwnProperty(key)) {
              const element = res.body.data[key];
              // this.addList.push({
              //   name: element.deviceName,
              //   id: element.deviceId
              // });
              for (const key2 in this.$store.default.state.accessManage.deviceList) {
                if (this.$store.default.state.accessManage.deviceList.hasOwnProperty(key2)) {
                  const device = this.$store.default.state.accessManage.deviceList[key2];
                  if (element.deviceId == device.id) {
                    // device.isAdd = false;

                    this.$set(device, "isAdd", "true");
                  }
                }
              }
            }
          }

          // this.pageNumber = res.body.pageNumber;
        });
    },
    selectHandleClose() {
      this.selectdialogVisible = false;
    }
  }
};
</script>

<style lang="scss">
.menu-tree{
  color: #7E8CB1;
  .el-tree-node__content:hover {
    background-color: #323847 !important;
  }
  .el-tree-node.is-current>.el-tree-node__content {
    color: #fff;
    background-color: #4741F2 !important;
  }
  .el-tree-node__content{
    span:nth-child(2){
        overflow-x: auto !important;
      }
  }
}
</style>



